import React, { useEffect, useState } from 'react';
import { Link } from 'gatsby';
import { FaAlignRight, FaTimes } from 'react-icons/fa';

import exordeLogo from '../images/pyramid.svg';
import Sidebar from './sidebar';

import content from '../../site/content/components/navbar.json';

const menu = [
  { id: 'home', label: 'Home', url: '' },
  { id: 'economics', label: 'Economics', url: '' },
  { id: 'français', label: 'Français', url: '/fr' },
  { id: 'anglais', label: 'English', url: '/en' },
];

interface Props {
  locale: string;
}

const Navbar = ({ locale }: Props) => {
  const [openSidebar, setOpenSidebar] = useState(false);
  const [scrollY, setScrollY] = useState(0);

  const onScroll = () => setScrollY(window.pageYOffset);

  useEffect(() => {
    const watchScroll = () => window.addEventListener('scroll', onScroll);
    watchScroll();
    return () => {
      window.removeEventListener('scroll', onScroll);
    };
  });
  return (
    <>
      <nav
        className={`fixed top-0 py-2 h-20 w-full flex items-center z-50 ${
          scrollY > 0
            ? `shadow bg-${content[locale].background_color} dark:bg-${content[locale].background_color_dark} text-white`
            : 'bg-transparent text-white'
        }`}
      >
        <div className="container">
          <Link to="/" className="h-28 flex items-center">
            <img src={exordeLogo} alt="exorde logo" className="h-12" />
            <h3 className={`font-display ml-2 text-2xl mt-4`}>EXORDE</h3>
          </Link>
          {/* <ul className="hidden md:flex ml-auto mr-auto">
          {menu.map(item => (
            <Link to={item.url}>
              <li className="p-2 hover:text-blue-dark hover:font-medium hover:border-b-2 hover:border-blue-dark">
                {item.label}
              </li>
            </Link>
          ))}
        </ul> */}
          {/* <button
          type="button"
          onClick={() => setOpenSidebar(!openSidebar)}
          className="ml-auto text-3xl text-blue md:invisible"
        >
          {openSidebar ? <FaTimes /> : <FaAlignRight />}
        </button> */}
        </div>
      </nav>
      <Sidebar open={openSidebar} />
    </>
  );
};

export default Navbar;
