import React from 'react';
import Layout from '../components/layout';

import content from '../../site/content/homepage.json';

interface Props {
  pageContext: {
    locale: string;
  };
}

const Home = ({ pageContext }: Props) => {
  const { locale } = pageContext;
  return (
    <Layout locale={locale}>
      <div id="homepage-content">
        {/* HERO PART */}
        <div
          className="container min-h-screen md:flex md:justify-between md:align-center md:pt-24"
          id="hero"
        >
          <div className="pt-32 mx-auto md:flex-1 md:pt-16">
            <h1 className="text-6xl text-blue font-display text-center dark:text-white md:text-left md:text-8xl">
              {content[locale].title}
            </h1>
            <p className="text-justify text-blue-light dark:text-grey">{content[locale].intro}</p>
          </div>
          <div className="flex container md:flex-1">
            <div
              className="border-2 border-blue rounded h-80 my-8 shadow mr-2 w-48 md:h-96 md:w-72 md:ml-24"
              id="news-card"
            >
              <div className="h-3/6">
                <img
                  src={content[locale].news[0].image}
                  className="h-full w-full object-cover rounded"
                />
              </div>
              <div className="p-2">
                <h3 className="text-blue dark:text-grey font-semibold">
                  {content[locale].news[0].title}
                </h3>
                <p className="truncate dark:text-grey-light text-sm md:whitespace-normal">
                  {content[locale].news[0].description}
                </p>
                <a
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-xs absolute mt-8 text-blue-light md:t-8"
                  href={content[locale].news[0].link}
                >
                  {content[locale].news[0].link}
                </a>
              </div>
            </div>
            <div className="my-8 w-8 flex flex-col justify-between md:max-h-96 md:mx-8">
              <div id="credibility" className="bg-blue w-24 h-24 rounded md:w-32 md:h-28">
                <h6 className="text-grey text-center pt-2">Credibility</h6>
                <h6 className="text-white text-center pt-6 text-5xl font-display">
                  {content[locale].news[0].credibility}%
                </h6>
              </div>
              <div id="sources" className="bg-blue-light w-24 h-24 rounded md:w-32 md:h-28">
                <h6 className="text-grey text-center pt-2">Similar on</h6>
                <ul className="text-white p-2 md:p-4">
                  <li> - BFM TV</li>
                  <li> - LCI </li>
                </ul>
              </div>
              <div id="virality" className="border-2 border-grey w-24 h-24 rounded md:w-32 md:h-28">
                <h6 className="text-grey text-center pt-2">Virality</h6>
                <h6 className="text-white text-center pt-4 text-2xl">High</h6>
              </div>
            </div>
          </div>
        </div>
        {/* STATS PART */}
        {content[locale].stats && (
          <div className="container md:flex md:flex-wrap md:justify-between" id="stats">
            {content[locale].stats.map(stat => (
              <div className="my-8 max-w-xs text-center mx-auto md:mx-0 md:p-8">
                <h2
                  className={`text-6xl text-${stat.title_color} dark:text-${stat.title_color_dark}`}
                >
                  {stat.title}
                </h2>
                <h4
                  className={`text-9xl text-${stat.number_color} dark:text-${stat.number_color_dark}`}
                >
                  {stat.number}
                </h4>
                <p
                  className={`text-justify text-${stat.description_color} dark:text-${stat.description_color_dark}`}
                >
                  {stat.description}
                </p>
              </div>
            ))}
          </div>
        )}
        {/* SECTIONS PART */}
        {content[locale].sections &&
          content[locale].sections.map(section => (
            <div
              className={`py-16 md:py-32 bg-${section.background_color} dark:bg-${section.background_color_dark}`}
            >
              <div className="container">
                <h1
                  className={`text-5xl sm:text-6xl md:text-9xl text-${section.title_color} dark:text-${section.title_color_dark}`}
                >
                  {section.title}{' '}
                  <b
                    className={`text-${section.keyword_color} dark:text-${section.keyword_color_dark}`}
                  >
                    {section.keyword}
                  </b>
                </h1>
                <p
                  className={`text-${section.description_color} dark:text-${section.description_color_dark}`}
                >
                  {section.description}
                </p>
                <div className="py-12">
                  {section.cards.map((card, i) => {
                    const reverse =
                      section.align == 'left' || (section.align == 'alternate' && i % 2 == 0);
                    return (
                      <div className={`py-8 md:flex md:py-12 ${reverse && 'md:flex-row-reverse'}`}>
                        <div className="my-4 md:flex-1">
                          <img
                            src={card.icon}
                            className={`icon-${card.icon_color} dark:icon-${card.icon_color_dark} mx-auto max-h-24`}
                          />
                        </div>
                        <div className="text-center md:flex-1 md:text-left">
                          <h2
                            className={`text-3xl text-${card.title_color} dark:text-${card.title_color_dark}`}
                          >
                            {card.title}
                          </h2>
                          <p
                            className={`text-${card.description_color} dark:text-${card.description_color_dark} max-w-lg`}
                          >
                            {card.description}
                          </p>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          ))}
        {/* USE CASES PART */}
        {content[locale].use_cases && (
          <div
            className={`py-16 md:py-32 bg-${content[locale].use_cases.background_color} dark:bg-${content[locale].use_cases.background_color_dark}`}
          >
            <div className="container">
              <h1
                className={`text-5xl sm:text-6xl md:text-9xl text-${content[locale].use_cases.title_color} dark:text-${content[locale].use_cases.title_color_dark}`}
              >
                {content[locale].use_cases.title}{' '}
                <b
                  className={`text-${content[locale].use_cases.keyword_color} dark:text-${content[locale].use_cases.keyword_color_dark}`}
                >
                  {content[locale].use_cases.keyword}
                </b>
              </h1>
              <p
                className={`text-${content[locale].use_cases.description_color} dark:text-${content[locale].use_cases.description_color_dark}`}
              >
                {content[locale].use_cases.description}
              </p>
              <div className="py-12 md:flex md:justify-between">
                {content[locale].use_cases.cards.map(card => (
                  <a
                    href={`${locale == 'en' ? '' : locale}${card.slug}`}
                    className="h-64 w-52 my-8 mx-auto relative md:h-72 md:w-64 block"
                  >
                    <h3 className="text-white absolute font-display text-4xl text-center pt-8 md:text-5xl">
                      {card.title}
                    </h3>
                    <img src={card.image} className="object-cover h-full rounded shadow" />
                  </a>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default Home;
